<template>
  <transition name="move">
    <div class="food" v-show="showFlag" ref="food">
      <div class="food-content">
        <!--  -->
        <div class="image-header">
          <img :src="food.image" alt="">
          <!--  -->
          <div class="back" v-on:click="hide">
            <i class="icon-arrow_lift icon-font"></i>
          </div>
        </div>
        <!---->
        <div class="content">
          <!---->
          <h1 class="title">{{food.name}}</h1>
          <!-- -->
          <div class="detail">
            <span class="sell-count">{{food.sellCount}}</span>
            <span class="rating">{{food.rating}}%</span>
          </div>
          <!---->
          <div class="price">
            <span class="now">{{food.price}}</span>
            <span class="old" v-show="food.oldPrice">{{food.oldPrice}}</span>
          </div>
          <!--  cart-control  -->
          <div class="cartcontrol-wrapper" v-show="food.count>0">
            <cartcontrol v-bind:food="food" @add="addFood"></cartcontrol>
          </div>
          <!--  -->
          <transition name="fade">
            <div class="buy" v-show="!food.count||food.count===0" v-on:click.stop.prevent="addFirst"></div>
          </transition>
        </div>
        <split v-show="food.info"></split>
        <!---->
        <div class="info" v-show="food.info">
          <h1 class="title"></h1>
          <p class="text">{{food.info}}</p>
        </div>
        <split></split>
        <!---->
        <div class="rating">
          <h1 class="title"></h1>
          <!--ratingselect-->
          <ratingselect
            @select="selectRating" @onlyContent="toggleContent" :selectType="selectType"
            :onlyContent="onlyContent" :desc="desc" :ratings="food.ratings">
          </ratingselect>
          <!---->
          <div class="rating-wrapper">
            <ul v-show="food.ratings && food.ratings.length">
              <li v-if="needShow(rating.rateType,rating.text)" v-for="rating in food.ratings"
                  class="rating-item border-1px">
                <div class="user">
                  <span class="name">{{rating.username}}</span>
                  <img class="avatar" width="12" height="12" :src="rating.avatar" alt="">
                </div>
                <div class="time">{{rating.rateTime | formatDate}}</div>
                <p class="text">
                  <i class="icon-font"
                     :class="{'icon-thumb_up':rating.rateType===1,'icon-thumb_down':rating.rateType===-1}"></i>
                  {{rating.text}}
                </p>
              </li>
            </ul>
            <div v-show="!food.ratings||!food.ratings.length" class="no-rating">
              
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import Bscroll from 'better-scroll';
  import cartcontrol from '../cartcontrol/cartcontrol.vue';
  import split from '../split/split.vue';
  import ratingselect from '../ratingselect/ratingselect.vue';
  import Vue from 'vue';
  import {formatDate} from '../../common/tool/date';

  const All = 0; // 
  export default {
    props: {
      food: {
        type: Object
      }
    },
    data() {
      return {
        showFlag: false,
        selectType: All,
        onlyContent: true,
        desc: {
          all: '',
          positive: '',
          negative: ''
        }
      };
    },
    methods: {
      show: function () {
        this.showFlag = true;
        this.selectType = All;
        this.onlyContent = false;
        this.$nextTick(function () {
          if (!this.scroll) {
            this.scroll = new Bscroll(this.$refs.food, {
              click: true
            });
          } else {
            this.scroll.refresh();
          }
        });
      },
      hide: function () {
        this.showFlag = false;
      },
      addFirst: function (event) {
        if (!event._constructed) {
          return;
        } else {
          // ,dom
          this.$emit('add', event.target);
          Vue.set(this.food, 'count', 1);
        }
      },
      addFood: function (target) {
        // ,dom
        this.$emit('add', event.target);
      },
      // type
      selectRating: function (type) {
        this.selectType = type;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      // onlyContent
      toggleContent: function () {
        this.onlyContent = !this.onlyContent;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      // 
      needShow: function (type, text) {
        // false
        if (this.onlyContent && !text) {
          return false;
        }
        // 
        if (this.selectType === All) {
          return true;
        } else {
          // 
          return type === this.selectType;
        }
      }
    },
    filters: {
      formatDate: function (time) {
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm');
      }
    },
    components: {
      cartcontrol: cartcontrol,
      split: split,
      ratingselect: ratingselect
    }
  };
</script>

<style>
  @import "food.css";
</style>



