<template>
  <div class="ratings" ref="ratings">
    <div class="ratings-content">
      <div class="overview">
        <!---->
        <div class="overview-left">
          <h1 class="score">{{seller.score}}</h1>
          <div class="title"></div>
          <div class="rank">{{seller.rankRate}}%</div>
        </div>
        <!---->
        <div class="overview-right">
          <div class="score-wrapper">
            <span class="title"></span>
            <star v-bind:size="36" v-bind:score="seller.serviceScore"></star>
            <span class="score">{{seller.serviceScore}}</span>
          </div>
          <div class="score-wrapper">
            <span class="title"></span>
            <star :size="36" v-bind:score="seller.foodScore"></star>
            <span class="score">{{seller.foodScore}}</span>
          </div>
          <div class="delivery-wrapper">
            <span class="title"></span>
            <span class="delivery">{{seller.deliveryTime}}</span>
          </div>
        </div>
      </div>
      <!--split-->
      <split></split>
      <!--ratingselect-->
      <rateselect
        :desc="desc"
        :ratings="ratings"
        :selectType="selectType"
        :onlyContent="onlyContent"
        @select="selectRating"
        @onlyContent="toggleContent"
      ></rateselect>
      <!---->
      <div class="ratings-wrapper">
        <ul>
          <li class="rating-item" v-for="rating in ratings" v-if="needShow(rating.rateType,rating.text)">
            <div class="avatar">
              <img :src="rating.avatar" width="28" height="28" alt="">
            </div>
            <div class="content">
              <div class="name">{{rating.username}}</div>
              <div class="star-wrapper">
                <star :size="24" :score="rating.score"></star>
                <div class="delivery">{{rating.deliveryTime}}</div>
              </div>
              <p class="text">{{rating.text}}</p>
              <div class="recommend" v-show="rating.recommend && rating.recommend.length">
                <span class="icon-thumb_up icon-font"></span>
                <span class="item" v-for="item in rating.recommend">{{item}}</span>
              </div>
              <div class="time">
                {{rating.rateTime | formatDate}}
              </div>
            </div>
          </li>
        </ul>
      </div>
      <!---->
      <div class="foot">
        <div class="title">
          <div class="line"></div>
          <div class="text"></div>
          <div class="line"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll';
  import star from '../../components/star/star.vue';
  import split from '../../components/split/split.vue';
  import rateselect from '../../components/ratingselect/ratingselect.vue';
  import {formatDate} from '../../common/tool/date';
  import data from '../../common/json/data.json';

  const All = 0; // 

  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data() {
      return {
        ratings: [],
        selectType: All,
        onlyContent: false,
        desc: {
          all: '',
          positive: '',
          negative: ''
        }
      };
    },
    components: {
      star: star,
      split: split,
      rateselect: rateselect
    },
    created() {
      this.ratings = data.ratings;
      this.$nextTick(function () {
        this.scroll = new BScroll(this.$refs.ratings, {
          click: true
        });
      });
    },
    methods: {
      // type
      selectRating: function (type) {
        this.selectType = type;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      // onlyContent
      toggleContent: function () {
        this.onlyContent = !this.onlyContent;
        this.$nextTick(() => {
          this.scroll.refresh();
        });
      },
      // 
      needShow: function (type, text) {
        // false
        if (this.onlyContent && !text) {
          return false;
        }
        // 
        if (this.selectType === All) {
          return true;
        } else {
          // 
          return type === this.selectType;
        }
      }
    },
    filters: {
      formatDate: function (time) {
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm');
      }
    }
  };
</script>

<style>
  @import "ratings.css";
</style>



