<template>
  <div>
    <div class="shopcart">
      <div class="content" v-on:click="toggleList($event)">
        <div class="content-left">

          <div class="logo-wrapper">
            <!---->
            <div class="logo" v-bind:class="{'highlight':totalCount>0}">
              <i class="icon-shopping_cart icon-font" v-bind:class="{'highlight':totalCount>0}"></i>
            </div>
            <!---->
            <div class="num" v-show="totalCount>0">
              {{totalCount}}
            </div>
          </div>
          <!---->
          <div class="price" v-bind:class="{'highlight':totalPrice>0}">{{totalPrice}}</div>
          <!---->
          <div class="desc">{{deliveryPrice}}</div>
        </div>
        <div class="content-right" v-on:click.stop.prevent="pay">
          <!---->
          <div class="pay" v-bind:class="payClass">
            {{payDesc}}
          </div>
        </div>
      </div>

      <!---->
      <div class="ball-container">
        <div v-for="ball in balls">
          <transition v-on:before-enter="beforeDrop" v-on:enter="dropping" v-on:after-enter="afterDrop">
            <div class="ball" v-show="ball.show">
              <div class="inner inner-hook"></div>
            </div>
          </transition>
        </div>
      </div>

      <!---->
      <transition name="fold">
        <div class="shopcart-list" v-show="listShow">
          <div class="list-header">
            <h1 class="title"></h1>
            <span class="empty" v-on:click="empty"></span>
          </div>
          <div class="list-content" ref="listContent">
            <ul>
              <li class="food" v-for="food in selectFoods">
                <span class="name">{{food.name}}</span>
                <div class="price">
                  <span>{{food.price * food.count}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol v-bind:food="food"></cartcontrol>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <!---->
    <transition name="fade">
      <div class="list-mask" v-show="listShow" v-on:click="hideList"></div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
  import cartcontrol from '../cartcontrol/cartcontrol.vue';
  import Bscroll from 'better-scroll';

  export default {
    components: {
      cartcontrol: cartcontrol
    },
    props: {
      // 
      deliveryPrice: {
        type: Number,
        default: 0
      },
      // 
      minPrice: {
        type: Number,
        default: 0
      },
      // goods,
      selectFoods: {
        type: Array,
        default: function () {
          return [
            {
              price: 30,
              count: 1
            }
          ];
        }
      }
    },
    data: function () {
      return {
        // 5
        balls: [{show: false}, {show: false}, {show: false}, {show: false}, {show: false}],
        // 
        dropBalls: [],
        // 
        fold: true
      };
    },
    computed: {
      // 
      totalPrice: function () {
        let total = 0;
        this.selectFoods.forEach(function (food) {
          total += food.price * food.count;
        });
        return total;
      },
      // 
      totalCount: function () {
        let count = 0;
        this.selectFoods.forEach(function (food) {
          count += food.count;
        });
        return count;
      },
      // 
      payDesc: function () {
        if (this.totalPrice === 0) {
          return '' + this.minPrice + '';
        } else if (this.totalPrice < this.minPrice) {
          let diff = this.minPrice - this.totalPrice;
          return '' + diff + '';
        } else {
          return '';
        }
      },
      // 
      payClass: function () {
        if (this.totalPrice < this.minPrice) {
          return 'not-enough';
        } else {
          return 'enough';
        }
      },
      // 
      listShow: function () {
        if (!this.totalCount) {
          this.fold = true;
          return false;
        } else {
          let show = !this.fold;
          if (show) {
            this.$nextTick(function () {
              if (!this.scroll) {
                this.scroll = new Bscroll(this.$refs.listContent, {
                  click: true
                });
              } else {
                this.scroll.refresh();
              }
            });
          }
          return show;
        }
      }
    },
    methods: {
      // 
      toggleList: function () {
        if (!this.totalCount) {
          return;
        }
        this.fold = !this.fold;
      },
      // 
      empty: function () {
        this.selectFoods.forEach(function (food) {
          food.count = 0;
        });
      },
      // 
      hideList: function () {
        this.fold = !this.fold;
      },
      // 
      pay: function () {
        if (this.totalPrice < this.minPrice) {
          return;
        } else {
          let money = this.totalPrice + this.deliveryPrice;
          window.alert('' + money + '');
        }
      },
      drop: function (el) {
        for (let i = 0; i < this.balls.length; i++) {
          let ball = this.balls[i];
          if (!ball.show) {
            ball.show = true;
            ball.el = el;
            this.dropBalls.push(ball);
            return;
          }
        }
      },
      beforeDrop(el) {
        let count = this.balls.length;
        while (count--) {
          let ball = this.balls[count];
          if (ball.show) {
            let rect = ball.el.getBoundingClientRect();// :top,right,bottom,left
            let x = rect.left - 32;// X
            let y = -(window.innerHeight - rect.top - 22); // Y
            el.style.display = '';
            //  el( 000)icon,(el)
            el.style.webkitTransform = `translate3d(0,${y}px,0)`;
            el.style.transform = `translate3d(0,${y}px,0)`;
            //   inner-hook, dom,
            let inner = el.getElementsByClassName('inner-hook')[0];
            inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
            inner.style.transform = `translate3d(${x}px,0,0)`;
          }
        }
      },
      dropping(el) {
        // ,translate3d,--rf 
        /* eslint-disable no-unused-vars */
        let rf = el.offsetHeight;
        this.$nextTick(function () {
          // 
          el.style.webkitTransform = 'translate3d(0,0,0)';
          el.style.transform = 'translate3d(0,0,0)';
          let inner = el.getElementsByClassName('inner-hook')[0];
          inner.style.webkitTransform = 'translate3d(0,0,0)';
          inner.style.transform = 'translate3d(0,0,0)';
        });
      },
      afterDrop(el) {
        // ,ball,ball,
        let ball = this.dropBalls.shift();
        if (ball) {
          ball.show = false;
          el.style.display = 'none';
        }
      }
    }
  };
</script>

<style>
  @import "shopcart.css";
</style>



